import React, { useState } from 'react';
import { Modal, Button } from 'antd';
import './modal1.css'
import VideoPlayer from '../home/Home';
interface ModalProps {
  visible: boolean;
  onClose: () => void;
}

const MyModal: React.FC<ModalProps> = ({ visible, onClose }) => {
  const [confirmLoading, setConfirmLoading] = useState(false);

  const handleOk = () => {
    setConfirmLoading(true);
    setTimeout(() => {
      setConfirmLoading(false);
      onClose();
    }, 2000); // 2 seconds delay for demonstration purposes
  };

  return (
    <Modal className='m'
      title="From SQUAD ONE"
      visible={visible}
      onOk={handleOk}
      onCancel={onClose}
      confirmLoading={confirmLoading}
    >
      <p>we will Contact you on our own</p>
    </Modal>
  );
};

const PModal: React.FC = () => {
  const [visible, setVisible] = useState(true);

  const showModal = () => {
    setVisible(true);
  };

  const handleOk = () => {
    setVisible(false);
  };

  const handleCancel = () => {
    setVisible(false);
  };

  return (
    <>
<VideoPlayer/>
      <Button type="primary" onClick={showModal}>
        Open Modal
      </Button>
      <MyModal visible={visible} onClose={handleOk} />
      
    </>
  );
};

export default PModal;
