import  { useEffect } from 'react'
import './about.css'
import VideoPlayer from '../home/Home';
const About = () => {
 
    useEffect(() => {
        const aboutContent = document.querySelector('#about-content');
        aboutContent?.scrollIntoView({ behavior: 'smooth' });
      }, []);

  return (
    <>
    <VideoPlayer/>
    <div className='about' id="about-content" >
    <div  className='parent-box'>
      <h1>ABOUT US!</h1>
       <p>Welcome to Squad One, a highly committed and professional group of individuals who are dedicated to achieving our collective goals through teamwork and collaboration. We believe that together we can accomplish great things, and we work tirelessly to ensure that every member of our team feels valued, respected, and empowered.

At Squad One, we place a strong emphasis on confidentiality and respect. We understand that the success of our group depends on our ability to communicate openly and honestly with one another, while also maintaining strict confidentiality regarding any information or discussions that take place within the group. We also recognize the importance of treating each other with professionalism and respect at all times, and we have zero tolerance for any form of harassment, discrimination, or bullying.</p>
   <p>Our group is committed to upholding the guidelines established by our leader, and we believe that every member has a critical role to play in contributing to our success. We understand that active participation is essential for achieving our goals, and we expect each member to make a meaningful contribution to the group's activities and objectives.

At the same time, we recognize that conflicts may arise within the group, and we have a clearly defined process for resolving these issues in a fair and equitable manner. We also understand the importance of consequences for violating group rules or guidelines, and we have a system in place for dealing with any such infractions in a timely and appropriate manner.</p>
    <p>Above all, Squad One is a team that is dedicated to achieving great things through our collective efforts. We are passionate, committed, and highly skilled, and we are proud to be a part of this dynamic and vibrant group.</p>
    </div></div></>
  )
}

export default About


