const HardcodedForm: React.FC = () => {
  const [answers, setAnswers] = useState<string[]>(questions.map(() => ""));
  const [disabled, setDisabled] = useState<boolean[]>(questions.map(() => false));


  

  const handleInputChange = (
    event: React.ChangeEvent<HTMLInputElement>,
    index: number
  ): void => {
    const newAnswers = [...answers];
    newAnswers[index] = event.target.value;
    setAnswers(newAnswers);
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>): void => {
    event.preventDefault();
    const data = questions.map((question, index) => {
      return {
        label: question.label,
        answer: answers[index],
      };
    });
    console.log("Answers submitted:", data);
    // Add code to submit data to Firebase
  };
  

  //Firebase connection
  const SubmitData = async (event: { preventDefault: () => void }) => {
    if (answers.some(answer => answer === "")) {
      // Don't submit if any input fields are empty
      alert("Please fill out all fields before submitting.");
      return;
    }
    const data = questions.map((question, index) => {
      return {
        label: question.label,
        answer: answers[index],
      };
    });
    setAnswers(questions.map(() => ""));
    event.preventDefault();
    const res = await fetch(
      "https://squad-one-32095-default-rtdb.firebaseio.com/squadOneMemeberData.json",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ data }), // Add the data array to the body
      }
    );
    if (res) {
      alert("data stored");
      
    } else alert("please fill the data");
  };
  

  return (
    <>
   <div className="parent"><form method="Post" onSubmit={handleSubmit}>
      {questions.map((question, index) => (
        <div key={question.id} className="question-container">
          <div className="question-text">{question.text}</div>
          <input
            type="text"
            className="answer-input"
            value={answers[index]}
            onChange={(event) => handleInputChange(event, index)}
            disabled={disabled[index]} />
        </div>
      ))}
      <button type="submit" className="submit-button" onClick={SubmitData}>
        Submit
      </button>
    </form></div>
    <VideoPlayer/></>
);
};
export default HardcodedForm;







  