
import ReactPlayer from 'react-player';
import './movie.css';

function Background() {
  return (
    <div className="video-wrapper">
      <ReactPlayer
        url={`${process.env.PUBLIC_URL}/background.mp4`}
        width="100%"
        height="100%"
        controls={false}
        playing={true}
        muted={true}
        loop={true}
      />
      <div className="logo-wrapper">
        <h1 className="logo">Squad One</h1>
      </div>
    </div>
  );
}

export default Background;
